import styled from "styled-components";
import { FaSearch as SearchIcon } from "react-icons/fa";
import { useState, useRef } from "react";
import { animated /* useSpring */ } from "react-spring";
import { MdClear as DeleteIcon } from "react-icons/md";

const placeholder = "";

type Props = {
  onChange: (value: string) => void;
  onFocus?: () => void;
  onBlur?: () => void;
  value?: string;
};

const SearchBox = ({ value, onChange, onFocus, onBlur }: Props) => {
  /*   const [focus, setFocus] = useState(false);
  const [hover, setHover] = useState(false);
 */ const inputRef = useRef(
    null
  );

  /* const props = useSpring({
    maxWidth: focus || hover ? 1000 : 350,
    config: { tension: 500 }
  }); */

  return (
    <SearchBoxArea
      onMouseEnter={() => {
        setHover(true);
        setFocus(true);
      }}
      onMouseLeave={() => {
        setHover(false);
        setFocus(false);
      }}
      onClick={() => {
        setFocus(true);
        onFocus && onFocus();
        inputRef.current.focus();
      }}
    >
      {/* <Buttons>
      {labels.map(label => <Button key={label}>{label}</Button>)}
    </Buttons> */}
      <SearchIcon
        size={22}
        color="rgba(255, 255, 255, 0.5)"
        style={{ margin: 8 }}
      />
      {/* <CategoryArea>
        <CategoryType>person:</CategoryType>
        <ValueType>Thomas</ValueType>
      </CategoryArea>
      <CategoryArea>
        <CategoryType>programming language:</CategoryType>
        <ValueType>C++</ValueType>
      </CategoryArea> */}

      <input
        style={{
          padding: "6px",
          display: "flex",
          flex: 1,
          borderRadius: "10px",
          backgroundColor: "transparent",
          borderStyle: "solid",
          borderColor: "rgba(255, 255, 255, 0.05)",
          borderWidth: "0px",
          flexFlow: "row wrap",
          margin: "2px",
          outline: "none",
          fontSize: "18px",
          color: "white",
          caretColor: "white",
          overflow: "hidden"
        }}
        ref={inputRef}
        type="text"
        autoCorrect="off"
        placeholder={placeholder}
        onChange={event => onChange(event.target.value)}
        value={value}
        onKeyDown={e => {
          if (e.key === "Enter") {
            (e.target as HTMLElement).blur();
          }
        }}
        onMouseOver={e => {
          (e.target as HTMLElement).focus();
        }}
        onFocus={() => {
          setFocus(true);
          onFocus && onFocus();
        }}
        onBlur={() => {
          setFocus(false);
          onBlur && onBlur();
        }}
      />
      {value && (
        <DeleteIcon
          size={22}
          color="rgba(255,255,255,0.5)"
          style={{ marginRight: 10 }}
          onClick={() => onChange("")}
        />
      )}
    </SearchBoxArea>
  );
};

export default SearchBox;

const SearchBoxArea = animated(styled.div`
  padding: 6px 10px;
  display: flex;
  flex: 1 1 450px;
  border-radius: 25px;
  background-color: rgba(255, 255, 255, 0.09);
  border-style: solid;
  border-color: rgba(255, 255, 255, 0.05);
  border-width: 0;
  flex-flow: row wrap;
  align-items: center;
  flex-wrap: nowrap;
  max-width: 1000px;
  margin: 4.5vw 0;
  /* transition: all 0.3s;
  &:hover {
    max-width: 1000px;
  } */
`);

